import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/Login'  //   import 
import MainTask from '@/MainTask'       // MainTask  import 

Vue.use(Router)

export default new Router({
  mode: 'history'
  , base: process.env.BASE_URL
  , routes: [
    {
      path: '/', //     
      name: 'Login',
      component: Login
    }
    , {
      path: '/mainTask', //     
      name: 'MainTask',
      component: MainTask
    }
  ]
})